@charset "UTF-8";
html, body {
  height: 100%;
  font-size: 10px; }

body {
  background: #ccc;
  font-size: .16rem; }

.wrapper {
  display: -webkit-box;
  /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
  display: -moz-box;
  /* 老版本语法: Firefox (buggy) */
  display: -ms-flexbox;
  /* 混合版本语法: IE 10 */
  display: -webkit-flex;
  /* 新版本语法: Chrome 21+ */
  display: flex;
  /* 新版本语法: Opera 12.1, Firefox 22+ */
  -webkit-box-direction: normal;
  -webkit-box-orient: vertical;
  -moz-flex-direction: column;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: stretch;
  -moz-align-items: stretch;
  -webkit-align-items: stretch;
  align-items: stretch;
  width: 100%; }

header {
  height: 1rem;
  font-size: 0.5rem;
  background-color: red; }

.content {
  -webkit-box-flex: 1.0;
  -moz-flex-grow: 1;
  -webkit-flex-grow: 1;
  flex-grow: 1; }
  .content dt {
    width: 1rem;
    height: 1rem;
    line-height: 1rem;
    float: left;
    background-color: yellow; }
  .content dd {
    width: 2.75rem;
    height: 1rem;
    line-height: 1rem;
    margin-left: 1rem;
    background-color: orange; }
    .content dd input {
      background: #0f0;
      font-size: 0.30rem;
      height: .9rem;
      width: 2rem; }
  .content .des {
    height: 0.6rem;
    font-size: 0.5rem; }

footer {
  height: 1rem;
  background-color: blue; }
